@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Sedan:ital@0;1&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  background-color: #f4f6f2;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

button {
  border: none;
  font-weight: normal;
  font-size: "Montserrat", sans-serif;
  cursor: pointer;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  font-family: "Sedan", serif;
  line-height: 1.2;
  font-weight: 400;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

.noticias .novedades {
  text-transform: uppercase;
  font-weight: bold;
}
.noticias h3 {
  text-transform: uppercase;
}

.leer-mas {
  display: inline-block;
  margin: 1rem 0;
  background-color: #082d0f;
  color: white;
  padding: 1rem 1.5rem;
  cursor: pointer;
}

.btn-volver {
  background-color: #2f6639;
  border: none;
  color: white;
  font-size: 1.7rem;
  padding: 1rem 2rem;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s;
  margin-bottom: 2rem;
}
.btn-volver:hover {
  background-color: #1f4325;
}

.recetas .card {
  box-sizing: border-box;
  padding: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  width: 27rem;
  justify-self: center;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: 0.3s ease, box-shadow 0.3s ease;
}
.recetas .card:hover {
  transform: translateY(-5px) translateX(5px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
}
.recetas .card .card-titulo {
  display: inline-block;
  font-family: "Sedan", serif;
  color: black;
  margin: 1rem 0;
  font-size: 2.5rem;
  text-decoration: underline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 27rem;
}
.recetas .card p {
  margin: 1rem 0;
}
.recetas .card img {
  width: 100%;
  height: auto;
  max-height: 35rem;
  object-fit: cover;
}
.recetas .card .card-link {
  margin-top: 1rem;
}

.card-tienda {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}
.card-tienda:hover {
  transform: translateY(-5px) translateX(5px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
}
.card-tienda h2 {
  font-size: 3rem;
}
.card-tienda p {
  margin: 1rem;
}
.card-tienda button {
  background-color: #082d0f;
  color: white;
  padding: 1rem;
  margin: 1rem 0;
  display: inline-block;
  width: 90%;
}

.admin-section h1 {
  margin: 2rem 0;
}

.nueva-receta {
  background-color: #2f6639;
  border: none;
  color: white;
  font-size: 1.7rem;
  padding: 1rem 2rem;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s;
  margin: 2rem 0;
}
.nueva-receta:hover {
  background-color: #1f4325;
}

.crear h1 {
  margin: 2rem 0;
}

.form-crear {
  margin: 2rem 0;
}
.form-crear fieldset {
  padding: 1rem 3rem;
}
.form-crear input {
  padding: 0.7rem;
  width: 100%;
}
.form-crear div {
  margin: 2rem 0;
}
.form-crear input[type=submit] {
  background-color: #2f6639;
  border: none;
  color: white;
  font-size: 1.7rem;
  padding: 1rem 2rem;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s;
}
.form-crear input[type=submit]:hover {
  background-color: #1f4325;
}

.contenedor {
  width: min(90%, 120rem);
  margin: 0 auto;
  background-color: #f4f6f2;
}

.seccion-blanco {
  background-color: white;
}

.seccion-negro {
  background-color: black;
}

.noticias {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 2rem;
  height: auto;
  max-height: 40rem;
  place-items: center;
}
.noticias .novedades {
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-weight: bold;
}
.noticias h3 {
  margin: 2rem 0;
  text-transform: uppercase;
}
.noticias p {
  margin: 2rem 0;
}
.noticias img {
  width: 100%;
  max-height: 30rem;
  object-fit: cover;
  margin: 2rem;
}

.recetas {
  background-color: white;
  padding-top: 2rem;
}
.recetas h2 {
  text-align: center;
  margin-bottom: 4rem;
}
.recetas .recetas-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.tienda {
  margin: 2rem 0;
}
.tienda h3 {
  text-align: center;
}
.tienda .contenedor-cards-tienda {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin: 2rem 0;
}
.tienda .contenedor-cards-tienda img {
  height: 40rem;
  object-fit: cover;
  width: 100%;
}

.sobre-nosotros-img {
  position: relative;
  overflow: hidden;
}
.sobre-nosotros-img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../../img/sobre-nosotros.webp");
  background-size: cover;
  background-position: center;
  opacity: 0.3;
  z-index: 1;
}

.sobre-nosotros {
  padding: 4rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  place-items: center;
  position: relative;
  z-index: 2;
  background-color: transparent;
}
.sobre-nosotros .sobre-nosotros-ubi {
  width: 100%;
  height: 100%;
  display: flex;
}
.sobre-nosotros h3, .sobre-nosotros p {
  text-align: center;
  margin: 2rem;
}
.sobre-nosotros iframe {
  width: 100%;
  height: 100%;
}

.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  height: 8rem;
  position: relative;
  z-index: 1;
}

.titulo {
  font-family: "Montserrat", sans-serif;
  font-size: 3rem;
  text-transform: uppercase;
  margin: 1rem;
  margin-left: 3rem;
}

.sub-titulo {
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
  margin-right: 1rem;
}

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media (max-width: 768px) {
  .header {
    height: 12rem;
    flex-direction: column;
  }
  .nav {
    padding: 1rem 0;
  }
}
@media (max-width: 550px) {
  .header {
    height: 25rem;
    justify-content: center;
  }
  .logo {
    flex-direction: column;
  }
  .nav ul {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 2rem;
    margin-left: 3rem;
  }
}
.recetas-header {
  position: relative;
  overflow: hidden;
}
.recetas-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-image: url("../../img/sobre-nosotros.webp");
  background-size: cover;
  background-position: center;
  opacity: 0.1;
  z-index: -1;
}

.footer a {
  color: white;
  display: inline-block;
  padding: 1rem 0;
}

.footer-grid {
  background-color: black;
  color: white;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 3rem 0;
  text-align: center;
  place-items: center;
}
.footer-grid .footer-mid {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.footer-grid .footer-mid p {
  text-transform: uppercase;
}
.footer-grid .footer-mid input {
  box-sizing: border-box;
  padding: 0.7rem;
  width: 100%;
  margin: 1rem 0;
}
.footer-grid .footer-mid button {
  width: 100%;
  padding: 0.7rem;
}

.copyright {
  color: white;
  text-align: center;
  font-size: 1.5rem;
  padding-bottom: 2rem;
}

.nav {
  display: flex;
  justify-content: flex-end;
  margin-right: 2rem;
}
.nav a {
  color: var(--negro);
}
.nav ul li {
  display: inline-block;
  margin-right: 1rem;
  transition: 0.3s ease;
}
.nav ul li:hover {
  background-color: #082d0f;
  color: white;
  padding: 0.3rem;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .nav {
    padding: 1rem 0;
  }
}
@media (max-width: 600px) {
  .nav ul {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 2rem;
    margin-left: 3rem;
  }
}